.form-field {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  color:rgba(2, 36, 65, 0.856);

}
.form-field>input{
  padding: 5px;
  margin-top: 3px;
  border-radius: 5px;
  outline: none;
  border: 2px transparent;
  transition: all 0.15s ;
}
.form-field>input:focus{
  border: rgba(2, 36, 65, 0.856);
}

.forma {
  display: grid ;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}

.textarea {
  grid-column: auto / span 2;
  margin-top: 3px;
  border-radius: 5px;
}

.forma-wrapper{
  padding: 25px;
  background-color: #c79f63ec;
  height: 360px;
  border-radius: 50px;
}
